*,
::after,
::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

html {
  font-size: 15px; }

body {
  font-family: 'Lato',sans-serif;
  background: whitesmoke;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

hr {
  opacity: 0.3; }

ul li {
  list-style-type: circle !important; }



